<template>
  <div class="echart-container">
      <div ref="content" class="echart-content"></div>
  </div>
</template>

<script>
export default {
    name:'EchartsImpl',
    data(){
        return {
            allData:[]
        }
    },
    methods:{
        //初始化echart实例对象
        initChart(){
            this.chartInstance = this.$echarts.init(this.$refs.content);
        },
        //ajax请求获取后台数据
        getData(){
            this.allData=[
                
            ]
        },
        //更新图表
        updateChart(){
            const option = {
                xAxis:{
                    type:'value'
                },
                yAxis:{
                    type:'category'
                },
                series:[
                    {
                        type:'bar'
                    }
                ]
            }
            this.chartInstance.setOption(option)
        }
    },
    mounted(){
        this.initChart()
        this.getData()
        this.updateChart()
    }
}
</script>

<style scoped lang="stylus" ref="stysheet/stylus">
.echart-container
    width 100%
    height 300px
    background-color #afb4db
    .echart-content
        width 100%
        height 100%
</style>